// @flow

import { type ExtendedKeyPair } from '../../types/hd.js'
import { toPublic } from '../core/privateKey.js'
import {
  getDecoder,
  getHDSetting
} from '../core/networkInfo.js'
import { hash160 } from '../utils/hash.js'
import { parseToken } from './path.js'
import * as Private from './private.js'
import * as Public from './public.js'

const MAX_DEPTH = 0xff

export const fromIndex = async (
  keyPair: ExtendedKeyPair,
  index: string
): Promise<ExtendedKeyPair> => {
  const { privateKey, depth, chainCode, version } = keyPair
  if (depth >= MAX_DEPTH) throw new Error('Depth too high.')

  const indexNum = parseToken(index)

  if (privateKey && !keyPair.publicKey) {
    console.log('4. fromIndex - toPublic', privateKey)
    keyPair.publicKey = await toPublic(privateKey, true)
  }
  const { publicKey } = keyPair

  let childKey

  if (privateKey) {
    childKey = await Private.derive(privateKey, indexNum, chainCode, false, publicKey)
  } else {
    childKey = await Public.derive(publicKey, indexNum, chainCode)
  }

  const parentFingerPrint = await hash160(publicKey)

  return {
    ...childKey,
    parentFingerPrint: parseInt(parentFingerPrint.slice(0, 8), 16),
    version,
    depth: depth + 1
  }
}

export const fromHex = (keyHex: string, network?: string): ExtendedKeyPair => {
  // Check the entire hex length
  if (keyHex.length !== 156) throw new Error('Wrong pair length')

  // Check that the key prefix matches the network's prefix if given or any network if not
  const version = parseInt(keyHex.slice(0, 8), 16)
  const { xpriv, xpub } = getHDSetting(version, network)

  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)

  const key = {}

  if (headerByte === 0 && version === xpriv.prefix) {
    key.privateKey = keyHex.slice(92, 156)
  } else if (
    (headerByte === 2 || headerByte === 3) &&
    version === xpub.prefix
  ) {
    key.publicKey = keyHex.slice(90, 156)
  } else {
    throw new Error('Bad key prefix')
  }

  return {
    ...key,
    version,
    depth: parseInt(keyHex.slice(9, 10), 16),
    parentFingerPrint: parseInt(keyHex.slice(10, 18), 16),
    childNumber: parseInt(keyHex.slice(18, 26), 16),
    chainCode: keyHex.slice(26, 90)
  }
}

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedKeyPair => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}
